.contactsBlock {
  padding: 140px 0;
  min-width: 300px;
  height: 100%;
}

.contactsContainer {
  /*display: block;*/
  flex-direction: column;

  /*display: flex;*/
}

.formAndContacts {
  display: flex;
  justify-content: space-between;
  width: 100%;
  flex-wrap: wrap;
}

.title {
  font-weight: 500;
  line-height: 1.2;
  font-size: 3rem;
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
}

.formContainer {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  margin: 0 auto;
  width: 100%;
  flex-wrap: wrap;


}

.nameFormBlock {
  display: flex;
  padding-bottom: 20px;
  flex-wrap: wrap;

}

.firstName {
  margin-right: 30px;
}

label {
  margin-bottom: 10px;
  font-weight: 400;
  font-family: "Nunito", sans-serif;
  line-height: 1.6;
  background: #ffffff;
  overflow-x: hidden;
  font-size: 16px;
  color: #939393;
}

input,
textarea {
  border: none;
  font-size: 16px;
  height: 45px;
  padding-left: 0;
  padding-right: 0;
  background: transparent;
  border-bottom: 1px solid #43434d;
  border-radius: 0;
  color: #000000;
  outline: none;
  height: 45px;
  font-family: 'Nunito', sans-serif;
  width: 100%;

  &:focus {
    border-bottom: 1px solid #96bb7c;
  }
}

.messageFormBlock {
  padding-top: 20px;
}

.messageInput {
  resize: none;
  min-height: 100px;
}


.button {
  margin-top: 15px;
  background-color: #96bb7c;
  padding: 15px 30px;
  border-radius: 24px;
  font-size: 11px;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: .05rem;
  border: none;
  color: #ffffff;
  cursor: pointer;
  font-family: 'Nunito', sans-serif;
}

.contactsInfoBlock {
  padding-right: 180px;
}

.contactsTitle {
  font-size: 1.25rem;
  margin-bottom: 20px;
  font-weight: 500;
}


ul li span {
  color: #96bb7c;
  list-style: none;
  padding-left: 16px;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 16px 16px; /* уменьшаем размер иконки до 24x24 пикселей */

}

ul li {
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
}

.address {
  font-style: normal;
  background-image: url('../../img/icons/location.svg');
}

.addressText {
  color: black;
}

.phone {
  background-image: url('../../img/icons/phone.svg');
}

.email {
  background-image: url('../../img/icons/email.svg');
}

.telegram {
  background-image: url('../../img/icons/telegramIcon.svg');
}


@media screen and (max-width: 1200px) {
  .contactsInfoBlock {
    padding-right: 0;
  }
}
@media screen and (max-width: 991px) {
  .contactsInfoBlock {
    margin-top: 30px;
  }
}

@media screen and (max-width: 768px) {
  .contactsBlock {
    padding: 70px 0;
    min-width: 300px;
    height: 100%;
  }
  .firstName {
    padding-bottom: 20px;
  }
  input,
  textarea {
  }

}